.postNav > a {
  width: calc(50% - 1.5em);
  color: #999;
  -webkit-transition: color 0.3s;
  -o-transition: color 0.3s;
  transition: color 0.3s; }
  .postNav > a:before {
    font-family: 'Montserrat', 'Helvetica Neue', Arial, sans-serif;
    font-weight: bold;
    display: block;
    font-size: 1em;
    margin-bottom: 0.5em;
    white-space: pre; }
  .postNav > a:hover {
    color: inherit; }

.postNav #newer {
  text-align: left;
  float: right; }
  .postNav #newer:before {
    content: "NEWER  >>"; }

.postNav #older {
  text-align: right;
  float: left; }
  .postNav #older:before {
    content: "<<  OLDER"; }

.postNav:after {
  content: '';
  clear: both;
  display: block; }



