.fade-enter {
  opacity: 0; }
  .fade-enter.fade-enter-active {
    opacity: 1;
    -webkit-transition: opacity ease-in;
    -o-transition: opacity ease-in;
    transition: opacity ease-in; }

.fade-exit {
  opacity: 1; }
  .fade-exit.fade-exit-active {
    opacity: 0;
    -webkit-transition: opacity ease-in;
    -o-transition: opacity ease-in;
    transition: opacity ease-in; }



